.intro{
    padding: 1.5rem 5%;
}
.intro-wrapper{
    display: flex;
    align-items: center;
}

.left-intro{
    flex: 1;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    gap: 2rem;
    height: 70vh;
}
.intro-content{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.intro-content >:nth-child(1){
    font-size: 2rem;
    font-weight: bold;
}
.intro-content >:nth-child(2){
    font-size: 3rem;
    font-weight: bold;
    color: var(--orange);
}
.intro-content >:nth-child(3){
    color: var(--gray);
}
.intro-content .intro-buton{
    width: fit-content;
    margin: 16px 0;
}
.intro-social{
    display: flex;
    gap: 1.5rem;
}
.intro-social img{
    width: 24px;
    cursor: pointer;
}


.right-intro{
    flex: 1;
    height: 70vh;
    position: relative;
    width: 100%;
}
.right-intro-wrapper{
    position: relative;
}
.right-intro-wrapper img{
    width: 100%;
}
.right-intro-wrapper img:nth-child(1){
    width: 65%;
    top: 4.5rem;
    left: 12%;
    position: absolute;
    z-index: 1;
}
.right-intro-wrapper img:nth-child(2){
    width: 60%;
    top: 5rem;
    left: 20%;
    position: absolute;
    z-index: 2;
}
.right-intro-wrapper img:nth-child(3){
    width: 48%;
    top: 0;
    left: 23%;
    position: absolute;
    z-index: 3;
}
.right-intro .upper{
    top: 2rem;
    right: 4rem;
    position: absolute;
    width: fit-content;
    display: flex;
    gap: 1rem;
    border-radius: 6px;
    padding: 10px 16px;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    z-index: 4;
    background-color: white;
}
.right-intro .upper img{
    width: 40px;
    height: 40px;
}
.right-intro .lower{
    bottom: 2rem;
    left: 4rem;
    position: absolute;
    width: fit-content;
    display: flex;
    gap: 1rem;
    border-radius: 6px;
    padding: 10px 16px;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    z-index: 4;
    background-color: white;
}
.right-intro .lower img{
    width: 40px;
    height: 40px;
}

.right-intro .glasses-image{
    position: relative;
    width: fit-content;
}
.right-intro .glasses-image img{
    width: 100px;
}



.intro-top-blur{
    background-color: rgb(250, 212, 237);
    top: -5%; 
    left: 50%;
}
.intro-bottom-blur{
    background-color: rgb(177, 220, 240);
    bottom: -5%; 
    left: -15%;
}


@media screen and (max-width: 576px){
    .intro{
        padding: .5rem 5%;
    }
    .intro-wrapper{
        flex-direction: column;
        text-align: center;
    }
    .left-intro{
        gap: 1rem;
        height: auto;
        align-items: center;
        order: 2;
        padding: 50px 0 30px;
    }
    .intro-content{
        display: flex;
        flex-direction: column;
        gap: 10px;
    }
    .intro-content >:nth-child(1){
        font-size: 16px;
        font-weight: bold;
    }
    .intro-content >:nth-child(2){
        font-size: 28px;
    }
    .intro-content >:nth-child(3){
        font-size: 14px;
        text-align: justify;
    }
    .intro-content{
        align-items: center;
    }
    .intro-social{
        gap: 10px;
    }
    .intro-social img{
        width: 16px;

    }
    



            
    .right-intro{
        order: 1;
        padding-bottom: 100px;
    }
    .right-intro-wrapper{
        height: 50vh;
        margin-top: 50px;
    }
    .right-intro-wrapper img{
        width: 100%;
    }
    .right-intro-wrapper img:nth-child(1){
        width: 85%;
        top: 2rem;
        left: 12%;
    }
    .right-intro-wrapper img:nth-child(2){
        width: 85%;
        top: 2rem;
        left: 10%;
    }
    .right-intro-wrapper img:nth-child(3){
        width: 65%;
        top: -2rem;
        left: 15%; 
    }
    .right-intro .upper,
    .right-intro .lower{
        gap: 8px;
        padding: 6px 8px;
        font-size: 12px;
    }
    .right-intro .upper img,
    .right-intro .lower img{
        width: 30px;
        height: 30px;
    }


    .right-intro .glasses-image{
        top: 1rem;
        left: 1rem;
        position: absolute;

    }
    .right-intro .glasses-image img{
        width: 100px;
    }

}