.projects{
    padding: 1rem 5%;
}
.projects-wrapper{
    display: flex;
    flex-direction: column;
}

.heading-p{
    margin: 0 auto;
}
.heading-p span{
    font-size: 2rem;
    font-weight: bold;
}


.slider-p img{
    width: 100%;
    height: 100%;
    cursor: pointer;
    overflow: hidden;
}
.projects-swiper{
    margin: 2rem 0;
    width: 100%;
}






.img-div-p{
    width: 290px;
    height: 160px;
    position: relative;
}

.tp-bg-p{
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    transform: scale(0);
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 1rem 2rem;
    transition: .3s;
    position: absolute;
    color: white;
    background-color: rgba(0,0,0,0.7);
}

.tp-bg-p span:nth-child(1){
    font-size: 16px;
    font-weight: bold;
    color: var(--orange);
}
.tp-bg-p span:nth-child(2){
    font-size: 12px;
}
.tp-bg-p span:nth-child(3){
    font-size: 12px;
}
.tp-bg-p .button-wrap-p{
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}
.tp-bg-p .button-wrap-p button{
    width: fit-content;
    border: none;
    padding: 8px;
    color: white;
    text-align: center;
    cursor: pointer;
    border-radius: 4px;
    font-size: 12px;
}
.tp-bg-p .button-wrap-p button:nth-child(1){
    background-color: rgb(44, 113, 241);
}
.tp-bg-p .button-wrap-p button:nth-child(1):hover{
    background-color: rgb(16, 84, 209);
}
.tp-bg-p .button-wrap-p button:nth-child(2){
    background-color: orange;
}
.tp-bg-p .button-wrap-p button:nth-child(2):hover{
    background-color: rgb(223, 146, 5);
}


.img-div-p:hover .tp-bg-p{
    transform: scale(1);
}







  
  @media screen and (max-width: 576px){
    .heading-p span{
        font-size: 24px;
    }
    .projects-swiper{
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }
}

