.contact{
    padding: 1.5rem 5%;
    overflow: hidden;
    padding-top: 100px;
}
.contact-wrapper{
    display: flex;
}

.left-contact{
    flex: 1;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
}
.left-contact span{
    font-size: 2rem;
    font-weight: bold;
}
.left-contact span:nth-child(2){
    color: var(--orange);
}



.right-contact{
    flex: 1;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.right-contact form{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.right-contact form input, 
.right-contact form textarea{
    padding: 8px 10px;
    border-radius: 5px;
    width: 300px;
    border: none;
    box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
    font-size: 13px;
}
.right-contact form button{
    width: 300px;
    padding: 8px 15px;
    color: white;
    background-color: orange;
    border-radius: 5px;
    text-align: center;
    border: none;
    outline: line;
}




.wave-bg-c{
    position: relative;
}
.contact-social{
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
   
}
.contact-social img{
    width: 32px;
    cursor: pointer;
}



.contact-blur-1{
    background-color: rgb(184, 225, 245);
    top: -50px; 
    left: 50%;
    transform: translateX(-50%);
}




@media screen and (max-width: 576px){
    .contact-wrapper{
        flex-direction: column;
        text-align: center;
    }

    .left-contact span{
        font-size: 24px;
    }
    .right-contact{
        padding: 1rem 0 3rem;
    }



    .contact-social{
        display: flex;
        flex-wrap: wrap;
        gap: 6px;
        top: 20%;
        left: 50%;
        transform: translate(-50%, -50%);
        position: absolute;
        
    }
    .contact-social img{
        width: 16px;
        cursor: pointer;
    }
}