.testimonials{
    padding: 1.5rem 5%;
}
.wrapper-testi{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;
    position: relative;
}
.heading-testi p{
    font-size: 2rem;
    font-weight: bold;
}
.heading-testi p span{
    color: var(--orange);
}



.t-blur1{
    background-color: rgb(114, 203, 245);
    top: 0; 
    left: 15%;
}
.t-blur2{
    background-color: rgb(250, 212, 237);
    bottom: -30%; 
    right: 5%;
}


@media screen and (max-width: 576px){
    .wrapper-testi{
        text-align: center;
        gap: 0;
    }
    .heading-testi p{
        font-size: 24px;
    }
}