.card-sr{
    width: 180px;
    height: auto;
    padding: 16px 10px;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    border: 5px solid rgba(255,165,0,0.4);
    border-radius: 10px;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.card-sr img{
    width: 40px;
    margin-bottom: 8px;
}
.card-sr h6{
    font-weight: bold;
}
.card-sr p{
    font-size: 13px;
}


@media screen and (max-width: 576px){
    .card-sr{
        width: 260px;
        border: 2px solid orange;
        border-radius: 10px;
    }
}