.carousel-testi{
    overflow: hidden;
    width: 500px;
    height: 230px;
    border-radius: 20px;
    background: rgba(255, 255, 255, 0.26);
    border: 7px solid var(--blueCard);
    box-shadow: var(--boxShadow);
}
.carousel-item-slider{
    width: 100%;
    height: 100%;
    padding: 1rem 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    border-radius: 20px;
}
.carousel-testi img{
    width: 70px !important;
    height: 70px;
    overflow: hidden;
    margin: 10px auto;
    border-radius: 50%;
}
.carousel-testi p{
    overflow: hidden;
    font-size: 14px;
    text-align: justify;
}


.carousel-indicators{
    margin-bottom: 1rem !important;
}
.carousel-control-prev-icon,
.carousel-control-next-icon {
    display: none;
}
.carousel-indicators button{
	background-color: orange !important;
    width: 8px !important;
    height: 8px !important;
    border-radius: 50%;
}


@media screen and (max-width: 576px){
    .carousel-testi{
        width: 100%;
        height: auto;
        border-radius: 10px;
        border: 3px solid var(--blueCard);
    }
}