.experience{
    padding: 1rem 5%;
}
.experience-wrapper{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    text-align: center;
    justify-content: center;
    gap: 1rem;
}

.item-ex{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    width: 200px;
    height: 250px;
    gap: 1rem;
}
.item-ex span:nth-child(1){
    background-color: white;
    padding: 1rem;
    border-radius: 100% 50%;
    font-weight: bold;
    font-size: 2rem;
    position: relative;
}
.item-ex span:nth-child(1)::before{
    content: '';
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    background-image: linear-gradient(rgb(127, 203, 238),rgb(243, 168, 217));
    width: 110%;
    height: 110%;
    border-radius: 100%;
    padding: 26px !important;
    z-index: -1;
    box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;

}
.item-ex span:nth-child(2){
    margin-top: 1rem;
    font-size: 20px;
}
.item-ex span:nth-child(3){
    color: var(--orange);
    font-size: 24px;
    font-weight: bold;
}



@media screen and (max-width: 576px){
    .item-ex{
        gap: .5rem;
        height: fit-content;
        margin: 10px 0;
    }
  
    .item-ex span:nth-child(2){
        margin-top: .5rem;
        font-size: 14px;
    }
    .item-ex span:nth-child(3){
        font-size: 18px;
    }

}