.my-navbar{
    height: 50px;
    z-index: 1000;
    padding: 40px 5%;
}
.nav-wrapper{
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.left-nav{
    flex: 1;
    display: flex;
    gap: 3rem;
}



.right-nav{
    flex: 1;
    display: flex;
    justify-content: space-between;
}
.nav-menu{
    align-items: center;
    display: flex;
}
.nav-menu ul{
    display: flex;
    align-items: center;
    gap: 1rem;
}
.nav-menu ul li:hover{
    color: var(--orange);
    cursor: pointer;
}


.contact-nav-btn{
    background-color: var(--orange);
    color: white;
    border: none;
    border-radius: 20px;
    padding: 4px 20px;
    font-size: 14px;
    height: fit-content;
    font-weight: bold;
}


@media screen and (max-width: 576px){
    .nav-menu{
        display: none;
    }

    .contact-nav-btn{
        right: 20px;
        position: absolute;
    }
}