.services{
    padding: 1rem 5%;
}
.services-wrapper{
    display: flex;
}

.left-services{
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    position: relative;
}
.left-services span:nth-child(2){
    color: var(--orange);
}
.left-services button{
    margin: 2rem 0;
    width: fit-content;
}




.right-services{
    flex: 1;
    position: relative;
}
.right-services .cards-s{
   display: flex;
   flex-wrap: wrap;
   justify-content: space-around;
   align-items: center;
}
.right-services .cards-s .cards-left div{
    position: relative;
}
.right-services .cards-s .cards-right{
    display: flex;
    flex-direction: column;
    gap: 100px;
  
}
.right-services .cards-s .cards-right div{
    position: relative;
}



.services-left-blur{
    background-color: rgb(250, 212, 237);
    bottom: 20%;
    left: 0;
}
.services-right-blur{
    background-color: rgb(177, 220, 240);
    top: 30%;
    right: 5%;
}



@media screen and (max-width: 576px){
    .services-wrapper{
        flex-direction: column;
        padding: 20px 0;
    }
    
    .left-services{
        gap: 0;
        align-items: center;
    }
    .left-services span{
        font-size: 24px;
    }
    .left-services p{
        font-size: 14px;
        text-align: justify;
        padding: 10px 0;
    }
    .left-services button{
        margin: 0 0 50px;
    }



    .right-services .cards-s{
        gap: 2rem 0;
    }
    .right-services .cards-s .cards-right{
        gap: 2rem 0;
    }
    
}