:root
{
  --yellow: #F5C32C;
  --orange : #FCA61F;
  --black : #242D49;
  --gray : #788097;
  --blueCard : #DDF8FE;
  --purple: rgb(238 210 255);
  --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
  --orangeCard: rgba(252, 166, 31, 0.45);
  --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}
html{
  scroll-behavior: smooth;
}

li{
  list-style: none;
}
h1,h2,h3,h4{
  font-weight: bold;
}


.my-btnG{
  color: white;
  background: linear-gradient(180deg, #fdc50f 26.71%, #fb982f 99.36%);
  border: none;
  border-radius: 25px;
  font-size: 16px;
  font-weight: bold;
  padding: 6px 20px;
  box-shadow: 0px 20px 24px 3px rgba(251, 161, 40, 0.42);
}
.my-btnG:hover{
  color: var(--orange);
  background: white;
  border: 1px solid var(--orange);
  padding: 5px 20px;
  cursor: pointer;
}


.blur{
  width: 20rem;
  height: 15rem;
  position: absolute;
  border-radius: 50%;
  z-index: -10;
  filter: blur(70px);
}

.large-text{
  font-size: 32px;
  font-weight: bold;
}
.mid-text{
  font-size: 24px;
  font-weight: bold;
}



@media screen and (max-width: 576px){
  .my-btnG{
    font-size: 14px;
    padding: 4px 20px;
  }
  .my-btnG:hover{
    padding: 3px 20px;
  }
}