.works{
    padding: 1rem 5%;
}
.works-wrapper{
    display: flex;
}

.left-works{
    flex: 1;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    gap: 1rem;
    position: relative;
}
.left-works span{
    font-size: 2rem;
    font-weight: bold;
}
.left-works span:nth-child(2){
    color: var(--orange);
}
.left-works button{
    width: fit-content;
    margin: 1.5rem 0;
}





.right-works{
    flex: 1;
    position: relative;
}
.main-circle-w{
    width: 280px;
    height: 280px;
    border-radius: 50%;
    margin: 50px auto;
    position: relative;
    background-color: white;
    box-shadow: var(--smboxShadow);
}
.sec-circle-w{
    width: 80px;
    height: 80px;
    padding: 1rem;
    border-radius: 50%;
    border: 5px solid #ebebeb;
    background-color: white;
    box-shadow: var(--smboxShadow);

}
.sec-circle-w img{
    width: 100%;
    height: 100%;
}

.main-circle-w .amazon-w{
    top: -40px;
    left: 50%;
    transform: translateX(-50%);
    position: absolute;
}
.main-circle-w .fiverr-w{
    top: 50%;
    right: -40px;
    transform: translateY(-50%);
    position: absolute;
}
.main-circle-w .shopify-w{
    bottom: -40px;
    left: 50%;
    transform: translateX(-50%);
    position: absolute;
}
.main-circle-w .upwork-w{
    top: 50%;
    left: -40px;
    transform: translateY(-50%);
    position: absolute;
}
.main-circle-w .fb-w{
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
}



.back-circle-blue-w{
    width: 200px;
    height: 200px;
    border-radius: 50%;
    background-color: blue;
    top: 1rem;
    right: 20%;
    position: absolute;
    z-index: -1;
}
.back-circle-orange-w{
    width: 200px;
    height: 200px;
    border-radius: 50%;
    background-color: orange;
    bottom: 1rem;
    right: 20%;
    position: absolute;
    z-index: -1;
}


.works-left-blur{
    background-color: rgb(177, 220, 240);
    bottom: 0; 
    right: 0;
}


@media screen and (max-width: 576px){
    .works-wrapper{
        flex-direction: column;
        text-align: center;
    }
    
    .left-works{
        gap: 0;
        align-items: center;
    }
    .left-works span{
        font-size: 24px;
    }
    .left-works p{
        font-size: 14px;
        text-align: justify;
        padding: 20px 0 0;
    }
    .left-works button{
        margin: 10px 0 2rem;
    }

    .main-circle-w{
        width: 220px;
        height: 200px;
    }
    .back-circle-blue-w{
        width: 150px;
        height: 150px;
        right: 0%;
    }
    .back-circle-orange-w{
        width: 150px;
        height: 150px;
        left: 0%;
    }
}